<template>
  <ColorPicker
    v-model="value"
    :disabled="config.disabled"
    :editable="config.editable"
    format="rgb"
    size="small"
    @on-change="change"
  />
</template>

<script>
export default {
  name: "ColorBox",
  props: {
    config: Object,
    __uuid: String
  },
  data() {
    return {
      value: ""
      // :disabled:true,
      // :editable:true,
      // :alpha:true,
      // :hue:true,
      // :recommend:true,
      // :colors:[],
      // :format:'hsl',
      // :size:'default'
    };
  },
  methods: {
    change(val) {
      console.log(val);
      this.$emit("change", {
        value: val,
        __uuid: this.__uuid
      });
    }
  },
  mounted() {
    this.change(this.value);
  }
};
</script>

